
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    color: {
        type: String
    },
    enabled: {
      type: Boolean,
      default: true
    },
    label: {
        type: String,
        required: true
    }
  },
  computed: {
    state(): string {
      return this.enabled ? `clickable ${this.color}` : 'disabled-card-color'
    }
  }
})
